.advant {
    &.bubble-trans.white-block {
        z-index: 7;

        & + section {
            z-index: 8;
        }

        & + section + section {
            z-index: 8;
        }
    }

    &__container {
    }

    &__head-text {
    }

    &__title {
    }

    &__text {
    }

    &__row {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: rem(10);

        @media(max-width:1024px){
            grid-template-columns: 1fr 1fr;
        }
        @media(max-width:650px){
            grid-template-columns: 1fr;
        }
    }

    &__item-row {
        display: flex;
        flex-direction: column;
        gap: rem(16);
        height: auto;
    }

    &__item-row-two {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: rem(16);
        grid-column: 2 span;

        @media(max-width:1024px){
            grid-template-columns: 1fr;
            grid-column: 1 span;
        }
    }

    &__item {
    }
}

