    &__link {
        position: relative;
        // display: inline-flex;
        display: flex;
        align-items: center;
        font-size: rem(16);
        gap: rem(4);
        width: max-content;
        padding-right: rem(22);
        margin-top: rem(16);
        color: #0d0d0d;
    
        .slide-anim {
            font-size: rem(16);
            min-width: rem(120);
        }

