        &::after {
            position: absolute;
            content: '';
            width:  rem(18);
            min-width:  rem(18);
            height: rem(19);
            -webkit-mask: url('data:image/svg+xml;utf8,<svg width="18" height="19" viewBox="0 0 18 19" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M14.25 9.36914L3.75 9.36914" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" /><path d="M9 14.6191L14.25 9.36914L9 4.11914" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" /></svg>') no-repeat center;
            mask: url('data:image/svg+xml;utf8,<svg width="18" height="19" viewBox="0 0 18 19" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M14.25 9.36914L3.75 9.36914" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" /><path d="M9 14.6191L14.25 9.36914L9 4.11914" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" /></svg>') no-repeat center;
            mask-size: contain;
            -webkit-mask-size: contain;
            background-color:  #0d0d0d;
            right: 0;
            top: rem(1);
            transition: background-color 0.3s ease  0s, right 0.3s ease  0s;
        }
        .slide-anim {
            &::after {
                font-weight: 400;
                color: var(--bs-mark-color);
            }
        }
    }
